import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = (error) => {
  store.dispatch('setErrorMessage', { title: 'Error', messages: [error] });
};

export default (uid, redeem) => {
  const request = Vue.$http.post(`/employee/${store.state.auth.currentUser.businessClientKey}/stamp_cards/${uid}/redeem.json`, redeem);

  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};


