/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';

export default {
  mixins: [
    slotMixin,
  ],
  props: {
    contextualStyle: {
      type: String,
      required: false,
    },
  },
  computed: {
    classNames() {
      let classNames = ['portlet'];

      if (this.contextualStyle) {
        classNames.push(this.contextualStyle);
      } else {
        classNames.push('light');
      }
      classNames.push('tasks-widget');
      return classNames;
    },
  },
};



