/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';
import store from 'app/store';
import Vue from 'vue';
import cancelStampPointService from 'services/cancel_stamp_point';
import languageService from 'services/language';

export default {
  mixins: [
    slotMixin,
  ],
  data() {
    return {
      stampPointUid: '',
    }
  },
  props: {
    stampPoints: {
      type: Array,
      required: false,
      default: () => [],
    },
    blackList: {
      type: Array,
      required: false,
      default: () => [],
    },
    stampCardStatus: {
      type: String,
      required: false,
      default: '',
    }
  },
  methods: {
    cancelPointForm() {
      store.dispatch('setLoading', true);
      const vmodal = this.$refs.modal;
      vmodal.hide();
      const stampCardUid = Vue.router.currentRoute.params.id;
      cancelStampPointService.invalidate(stampCardUid, this.stampPointUid)
      .then((p) => {
        store.dispatch('setLoading', false);
        if (p.success) {
          store.dispatch('setErrorMessage', { type: 'info', messages: ['Invalidate stamp point was successfully updated.'] });
          window.location.reload();
        } else {
          store.dispatch('setErrorMessage', { type: 'danger', messages: p.messages });
        }
      })
    },
    canPointCancel(stampCardStatus, stampPointStatus) {
      if (stampCardStatus != 'redeemed' && stampPointStatus != 'invalidated') {
        return true;
      }
      return false;
    },
    modalAlert(stampPointUid) {
      const vmodal = this.$refs.modal;
      vmodal.show();
      this.stampPointUid = stampPointUid;
    },
    allowKey(key) {
      return this.blackList.indexOf(key) === -1;
    }
  },
  filters: {
    capitalize(value) {
      if (!value) return '';
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
    humanize(value) {
      return value
      .replace(/([A-Z])/g, ' $1')
      .replace(/^./, function(str){ return str.toUpperCase(); })
      .replace('_', ' ');
    },
    formatDate: function(date) {
      let language = languageService.get();
      return moment(date).locale(language).format('ll');
    }
  },
  components: {
    VStatusLabel: require('components/status_label/component.vue'),
    VModal: require('components/modal/component.vue'),
  }
};



