/* ============
 * Vuex Actions
 * ============
 *
 * All the actions that can be used
 * inside the store
 */
import * as types from './mutation-types';

// Auth
export const login = ({ commit }, user) => {
  commit(types.LOGIN, user);
};

export const logout = ({ commit }, message) => {
  commit(types.LOGOUT, message);
};

export const checkAuthentication = ({ commit }) => {
  commit(types.CHECK_AUTHENTICATION);
};

// POST
export const listPost = ({ commit }, posts) => {
  commit(types.LIST_POST, posts);
};

export const getPost = ({ commit }, post) => {
  commit(types.CURRENT_POST, post);
};

// General
export const postClearState = ({ commit }) => {
  commit(types.CLEAR_POST);
};

// General
export const setBreadcrumb = ({ commit }, breadcrumbs) => {
  commit(types.SET_BREADCRUMB, breadcrumbs);
};

export const setPageTitle = ({ commit }, title) => {
  commit(types.SET_PAGE_TITLE, title);
};

export const setPageDescription = ({ commit }, desc) => {
  commit(types.SET_PAGE_DESCRIPTION, desc);
};

export const setLoading = ({ commit }, loading) => {
  commit(types.SET_LOADING, loading);
};

export const setErrorMessage = ({ commit }, error) => {
  commit(types.SET_ERROR_MESSAGE, error);
};

export const setBeforeLoginUrl = ({ commit }, url) => {
  commit(types.SET_BEFORE_LOGIN_URL, url);
};

// Business client
export const setBusinessClientLogo = ({ commit }, logo) => {
  commit(types.SET_BUSINESS_CLIENT_LOGO, logo);
};



