import {
  SET_PAGE_TITLE,
  SET_PAGE_DESCRIPTION,
  SET_LOADING,
  SET_ERROR_MESSAGE,
  SET_BEFORE_LOGIN_URL,
} from './../../mutation-types';

export default {
  [SET_PAGE_TITLE](state, title) {
    state.pageTitle = title;
  },
  [SET_PAGE_DESCRIPTION](state, desc) {
    state.pageDescription = desc;
  },
  [SET_LOADING](state, loading) {
    state.loading = loading;
  },
  [SET_ERROR_MESSAGE](state, error) {
    state.error = error;
  },
  [SET_BEFORE_LOGIN_URL](state, url) {
    state.beforeLoginUrl = url;
  },
};



