/* ============
 * Login Index Page
 * ============
 *
 * Page where the user can login
 */

import store from 'app/store';
import AuthService from './../../../services/auth';
import languageService from './../../../services/language';
const image = require('images/cnt-logo.png');

export default {
  data() {
    return {
      employee: {
        email: null,
        password: null,
      },
      logoImg: image,
      language: languageService,
    };
  },
  mounted() {},
  methods: {
    login(employee) {
      if (!employee.email || !employee.password) {
        store.dispatch('setErrorMessage', {
          type: 'danger',
          messages: [
            this.$t('alertMessage.login.pleaseInputUsernameAndPassword'),
          ],
        });
        return false;
      }
      AuthService.login(employee);
    },
  },
  components: {
    VLayout: require('layouts/login/login.vue'),
    VAlert: require('components/alert/component.vue'),
    VLanguage: require('components/language/component.vue'),
  },
};



