/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';

export default {
  mixins: [
    slotMixin,
  ],
  data() {
    return {
      hamburgerImg: require("assets/metronic/assets/layouts/layout/img/m_toggler.png"),
      overlayShow: false,
    }
  },
  methods: {
    triggerClick() {
      this.overlayShow = true;
    },
    removeModal(){
      this.overlayShow = false;
    },
  }
};



