/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

 import slotMixin from './../../mixins/slot';

 export default {
   mixins: [
     slotMixin,
   ],
   props: {
     modalId: {
       Type: String,
       required: false,
       default: 'defaul-modal-id',
     },
   },
   methods: {
     show() {
       $('#' + this.modalId).modal('show');
     },
     hide() {
       $('#' + this.modalId).modal('hide');
     }
   }
 };



