/* ============
 * Bootstrap File
 * ============
 *
 * Will configure and bootstrap the application
 */

/* ============
 * Vue
 * ============
 *
 * Vue.js is a library for building interactive web interfaces.
 * It provides data-reactive components with a simple and flexible API.
 *
 * http://rc.vuejs.org/guide/
 */
import Vue from 'vue';

Vue.config.debug = process.env.NODE_ENV !== 'production';

/* ============
 * Axios
 * ============
 *
 * Promise based HTTP client for the browser and node.js.
 * Because Vue Resource has been retired, Axios will now been used
 * to perform AJAX-requests.
 *
 * https://github.com/mzabriskie/axios
 */
import Axios from 'axios';
import authService from './app/services/auth';

Axios.defaults.baseURL = process.env.API_LOCATION;
Axios.defaults.headers.common.Accept = 'application/json';
Axios.defaults.withCredentials = true;
Axios.interceptors.request.use(config => {
  config.headers.locale = Vue.config.lang;
  return config;
}, error => error);
Axios.interceptors.response.use(
  response => response,
  error => {
    if (error.response.status === 401) {
      authService.logout(error.response.data.error);
    }
    return Promise.reject(error.response.data);
  }
);
Vue.$http = Axios;

/* ============
 * Vuex Router Sync
 * ============
 *
 * Effortlessly keep vue-Router and vuex store in sync.
 *
 * https://github.com/vuejs/vuex-router-sync/blob/master/README.md
 */
import VuexRouterSync from 'vuex-router-sync';
import store from './app/store';

store.dispatch('checkAuthentication');

/* ============
 * Vue Router
 * ============
 *
 * The official Router for Vue.js. It deeply integrates with Vue.js core
 * to make building Single Page Applications with Vue.js a breeze.
 *
 * http://router.vuejs.org/en/index.html
 */
import VueRouter from 'vue-router';
import routes from './app/routes';

Vue.use(VueRouter);

export const router = new VueRouter({
  routes,
});

router.beforeEach((to, from, next) => {
  if (from.name === 'login.index') {
    store.dispatch('setBeforeLoginUrl', null);
  }

  if (to.matched.some(m => m.meta.auth) && !store.state.auth.authenticated) {
    store.dispatch('setBeforeLoginUrl', {
      name: to.name,
      params: to.params,
      query: to.query,
    });

    next({
      name: 'login.index',
    });
  } else {
    if (from.meta.clearState) {
      from.meta.clearState();
    }
    next();
  }
});

router.afterEach(() => {
  store.dispatch('setErrorMessage', {});
});

VuexRouterSync.sync(store, router);

Vue.router = router;

/* ============
 * Vue i18n
 * ============
 *
 * Internationalization plugin of Vue.js
 *
 * https://kazupon.github.io/vue-i18n/
 */
import VueI18n from 'vue-i18n';
import locale from './app/locale';

Vue.use(VueI18n);

Vue.config.lang = 'en';

Object.keys(locale).forEach(lang => {
  Vue.locale(lang, locale[lang]);
});

