/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';

export default {
  data() {
    return {
      message: ''
    }
  },
  methods: {
    fetch() {
      console.log(Vue.router.currentRoute.params);
      this.message = Vue.router.currentRoute.params.message;
    }
  },
  mounted() {
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
  }
}



