import {
  CURRENT_POST,
  LIST_POST,
  CLEAR_POST,
} from './../../mutation-types';

export default {
  [LIST_POST](state, posts) {
    state.list = posts;
  },
  [CURRENT_POST](state, post) {
    state.current = post;
  },
  [CLEAR_POST](state) {
    state.list = [];
    state.current = { name: null, content: null };
  },
};



