/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import StampPointHistoryService from 'services/history';

export default {
  data() {
    return {
      stampPoints: [],
      blackList: ['uid','can_invalidate'],
    }
  },
  methods: {
    fetch() {
      store.dispatch('setLoading', true);
      StampPointHistoryService.get(Vue.router.currentRoute.params.id)
      .then((res) => {
        this.stampPoints = res;
        store.dispatch('setLoading', false);
      })
    },
  },
  mounted() {
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VStampPoints: require('components/stamp_points/component.vue'),
  }
}



