/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */
import store from 'app/store';
import MemberService from 'services/member';

export default {
  data() {
    return {
      keyword: "",
      members: []
    }
  },
  methods: {
    searchForm(keyword) {
      store.dispatch('setLoading', true);
      MemberService.search(keyword)
      .then((members) => {
        this.members = members;
        store.dispatch('setLoading', false);
      })
    },
    fullName(member) {
      let fullName = [];
      if(member.title) fullName.push(member.title);
      if(member.first_name) fullName.push(member.first_name);
      if(member.last_name) fullName.push(member.last_name);
      fullName = fullName.join(" ");
      if(fullName) return fullName;
      return "NO NAME";
    },
  },
  components: {
    VPanel: require('components/panel/component.vue'),
  },
};



