import Vue from 'vue';
import store from './../../store';

const success = (response) => {
  // console.log(response);
  Vue.router.replace({
    name: 'login.index',
  });
  store.dispatch('setErrorMessage', { type: 'info', messages: [response.message] });
};

// When the request fails
const failed = (error) => {
  Vue.router.replace({
    name: 'password.forgotPassword',
  });
  store.dispatch('setErrorMessage', { type: 'danger', messages: [error.message] });
};

export default (employee) => {
  Vue.$http.post('/p/employees/reset_password.json', { employee })
  .then((response) => {
    success(response.data);
  })
  .catch((error) => {
    failed(error);
  });
};



