/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';
import languageService from 'services/language';

export default {
  mixins: [
    slotMixin,
  ],
  props: {
    stampCard: {
      type: Object,
      required: false,
      default: {},
    },
    blackList: {
      type: Array,
      required: false,
      default: () => [],
    },
    registrationFields: {
      type: Array,
      required: false,
      default: () => [],
    },
  },
  methods: {
    allowKey(key) {
      return this.blackList.indexOf(key) === -1;
    },
    checkValueIsNull(value) {
      if (value != null) {
        return value;
      }
      return '';
    },
    fullName() {
      if (this.stampCard.first_name) {
        let fullName = this.checkValueIsNull(this.stampCard.title) + ' ' + this.checkValueIsNull(this.stampCard.first_name) + ' ' + this.checkValueIsNull(this.stampCard.last_name)
        return fullName;
      }
      return this.$t("stampCard.notRegistered");
    }
  },
  filters: {
    capitalize(value) {
      if (!value) return '';
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
    humanize(value) {
      return value
      .replace(/([A-Z])/g, ' $1')
      .replace(/^./, function(str){ return str.toUpperCase(); })
      .replace('_', ' ');
    },
    formatDate: function(date) {
      let language = languageService.get();
      return moment(date).locale(language).format('ll HH:mm');
    },
  },
  components: {
    VStatusLabel: require('components/status_label/component.vue'),
  }
};



