/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';

export default {
  mixins: [
    slotMixin,
  ],
  props: {
    status: {
      type: String,
      required: true,
      default: '',
    },
  },
  methods: {
    classNames() {
      const classNames = ['badge','uppercase', 'ax-status-font-size'];

      const STATUS_COLORS = { "active": "bg-green",
                              "inactive": "bg-red",
                              "invalidated": "bg-red",
                              "redeemed": "bg-grey-salt",
                              "member_generated": "badge bg-blue-steel"}

      if (STATUS_COLORS[this.status]){
        classNames.push(STATUS_COLORS[this.status]);
      }
      return classNames.join(' ');
    },
  }
};



