import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = () => {
  store.dispatch('setErrorMessage', { type: 'danger', title: '', message: 'Cannot create visit' });
};

export default (data) => {
  // need -> offerUid, memberUid, visitTypeUid, shopUid (in data)
  const visit = {
    visit_type_uid: data.visitTypeUid,
    shop_uid: data.shopUid,
    offer_uid: data.offerUid,
  };
  const businessClientKey = store.state.auth.currentUser.businessClientKey;
  const request = Vue.$http.post(`/employee/${businessClientKey}/members/${data.memberUid}/visits.json`, visit);
  return request
  .then(response => response.data)
  .catch((error) => {
    failed(error);
  });
};



