import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = () => {
};

export default (id) => {
  const request = Vue.$http.get(`/employee/${store.state.auth.currentUser.businessClientKey}/stamp_cards/${id}/stamp_points/history.json`);
  return request
  .then(response => response.data)
  .catch((error) => {
    failed(error);
  });
};

