/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import StampPointService from 'services/stamp_point';

export default {
  data() {
    return {
      shops: [],
      stampImages: {},
      totalPoints: 0,
      addedPoint: {},
      point: 1,
      amount: '0.00',
      modalPoint: 0,
      selectedShopUid: '',
      cardAmount: 0,
      isAmount: '',
      amountPerStamp: 0,
    }
  },
  methods: {
    sumPoints() {
      const sumPoints = parseInt(this.totalPoints) + parseInt(this.point);
      const result = sumPoints % this.totalPointEachCard();
      if(result === 0 && sumPoints !== 0){
        return this.totalPointEachCard();
      }
      return result;
    },
    getCardAmount() {
      if(this.totalPointEachCard() < 0) return 1;
      const sumPoints = parseInt(this.totalPoints) + parseInt(this.point);
      return Math.ceil(sumPoints / this.totalPointEachCard());
    },
    totalPointEachCard() {
      return Object.keys(this.stampImages).length - 2;
    },
    modalToggle() {
      if (this.isAmount) {
        this.modalPoint = Math.floor(this.amount / this.amountPerStamp);
      } else {
        this.modalPoint = this.point;
      }
    },
    submitForm() {
      const vmodal = this.$refs.modal;
      vmodal.hide();
      const stampCardUid = Vue.router.currentRoute.params.id;
      let stampPoint;
      if (this.isAmount) {
        stampPoint = {shop_uid: this.selectedShopUid, amount: this.amount };
      } else {
        stampPoint = {shop_uid: this.selectedShopUid, point: this.point };
      }
      StampPointService.create(stampCardUid, stampPoint)
      .then((p) => {
        if (p.success) {
          Vue.router.replace({ name: 'stampPoint.index', params: { id: stampCardUid } })
          store.dispatch('setErrorMessage', { type: 'info', messages: [this.$t("alertMessage.stampPoint.successfullyCreated")] })
        } else {
          store.dispatch('setErrorMessage', { type: 'danger', messages: p.messages })
        }
      })
    },
    fetch() {
      store.dispatch('setLoading', true);
      StampPointService.get(Vue.router.currentRoute.params.id)
      .then((res) => {
        this.shops = res.shops;
        if (res.shops.length > 0) {
          this.selectedShopUid = res.shops[0].uid;
        }
        this.stampImages = res.stamp_images;
        this.totalPoints = res.total_points;
        this.isAmount = res.is_amount;
        this.amountPerStamp = res.amount_per_stamp;
        store.dispatch('setLoading', false);

        if (!this.isAmount) {
          $('#point').TouchSpin({
            min: 1,
            initval: 1,
            buttondown_class: 'btn red-mint',
            buttonup_class: 'btn green-sharp',
          }).on('change', () => {
            this.point = $('#point').val();
            if(this.point == 1) {
              $('.bootstrap-touchspin-down').removeClass('red-mint');
              $('.bootstrap-touchspin-down').addClass("grey");
            } else {
              $('.bootstrap-touchspin-down').removeClass("grey");
              $('.bootstrap-touchspin-down').addClass("red-mint");
            }
          });
          $("#point").change();
        }
      })
    }
  },
  mounted() {
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    },
    amount(newVal, oldValue) {
      if(newVal !== oldValue){
        this.amount = newVal.replace(/[^\d.-]/g, '');
      }
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VModal: require('components/modal/component.vue'),
  },
}



