/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

 import store from 'app/store';
 import StampTemplateService from 'services/stamp_template';
 import Vue from 'vue';

 export default {
   data() {
     return {
       stampTemplates: [],
     }
   },
   methods: {
    fetch() {
      store.dispatch('setLoading', true);
      StampTemplateService.list()
      .then((res) => {
        this.stampTemplates = res;
        if (this.stampTemplates.length == 1) {
          Vue.router.push({ name: 'stampTemplate.show', params: { shop_id: '$store.state.route.params.shop_id', id: this.stampTemplates[0].uid } });
        }
        store.dispatch('setLoading', false);
      })
    },
   },
   mounted() {
     this.fetch();
   },
   watch: {
     '$route': {
       handler: function() {
         this.fetch();
       },
       deep: true
     }
   },
   components: {
     VPanel: require('components/panel/component.vue'),
   }
 }



