import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = (error) => {
  store.dispatch('setErrorMessage', { type: 'danger', messages: error.messages });
};

// export default (uid, tmpMember, tmpTags) => {
export default (uid, tmpMember) => {
  const member = {
    profile_image: (!tmpMember.profileImage.startsWith('http') ? tmpMember.profileImage : ''),
    // membership_number: tmpMember.membershipNumber,
    title: tmpMember.title,
    first_name: tmpMember.firstName,
    last_name: tmpMember.lastName,
    day_of_birth: tmpMember.dayOfBirth,
    month_of_birth: tmpMember.monthOfBirth,
    year_of_birth: tmpMember.yearOfBirth,
    identification_number: tmpMember.identificationNumber,
    email: tmpMember.email,
    telephone: tmpMember.telephoneNumber,
    member_member_fields: tmpMember.memberMemberField,
    // tag_list: tmpTags,
  };
  const request = Vue.$http.patch(`/employee/${store.state.auth.currentUser.businessClientKey}/members/${uid}.json`, { member });
  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};



