import Vue from 'vue';
import store from './../../store';

// When the request succeeds
const success = employee => {
  store.dispatch('login', employee);
  if (!!store.state.general.beforeLoginUrl) {
    return Vue.router.push(store.state.general.beforeLoginUrl);
  }
  Vue.router.push({ path: '/' });
};

// When the request fails
const failed = error => {
  if (!error) {
    store.dispatch('setErrorMessage', {
      type: 'danger',
      messages: ['Please input username and password'],
    });
  } else {
    store.dispatch('setErrorMessage', {
      type: 'danger',
      messages: [error.error],
    });
  }
};

export default employee => {
  if (!employee.email || !employee.password) {
    failed();
  } else {
    Vue.$http
      .post('/employees/sign_in.json', { employee })
      .then(response => {
        success(response.data);
      })
      .catch(failed);
  }
};



