import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = (error) => {
  store.dispatch('setErrorMessage', { title: 'Error', messages: [error] });
};

export default (stampCardUid, uid) => {
  const businessClientKey = store.state.auth.currentUser.businessClientKey;
  const request = Vue.$http.post(`/employee/${businessClientKey}/stamp_cards/${stampCardUid}/stamp_points/${uid}/invalidate.json`);

  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};

