/* ============
 * Default Layout
 * ============
 *
 * Used for the home and account pages
 *
 * Layouts are used to store a lot of shared code.
 * This way the app stays clean.
 */

import store from 'app/store';
import authService from './../../services/auth';
import languageService from './../../services/language';
import BusinessClientService from './../../services/business_client';

export default {
  mounted() {
    $('body').css('background-color', '#f1f3fa');
  },
  data() {
    return {
      language: languageService,
    }
  },
  methods: {
    fetch() {
      store.dispatch('setLoading', true);
      BusinessClientService.get()
      .then((res) => {
        store.dispatch('setBusinessClientLogo', res);
        store.dispatch('setLoading', false);
      })
    },
    logout() {
      authService.logout();
    },
  },
  created() {
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VNavBar: require('components/navbar/component.vue'),
    VSideBar: require('components/sidebar/component.vue'),
    VPageBar: require('components/page_bar/component.vue'),
    VPageTitle: require('components/page_title/component.vue'),
    VAlert: require('components/alert/component.vue'),
  }
};



