/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';
import store from 'app/store';

export default {
  mixins: [
    slotMixin,
  ],
  data() {
    return {
      logoImg: require("images/ml-logo.png"),
      profileImg: require("images/user-icon.png"),
    }
  },
  props: {
    logout: {
      type: Function,
      required: true,
    },
    user: {
      type: Object,
      required: true,
    },
    language: {
      type: Object,
      required: true,
    }
  },
  components: {
    VMenuOverlay: require('components/menu_overlay/component.vue'),
    VLanguage: require('components/language/component.vue')
  },
  methods: {
    fullName(){
      let fullName = [];
      if(this.user.firstName) fullName.push(this.user.firstName);
      if(this.user.lastName) fullName.push(this.user.lastName);
      fullName = fullName.join(" ");
      if(fullName) return fullName;
      return "NO NAME";
    },
    setBusinessClientLogo(){
      if (store.state.businessClient.businessClientLogo) {
        return store.state.businessClient.businessClientLogo;
      } else {
        return this.logoImg;
      }
    }
  },
};



