import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = () => {
};

export default (uid, passkitPassUid) => {
  const request = Vue.$http.get(`/employee/${store.state.auth.currentUser.businessClientKey}/shop_employee_offers/${uid}/show_offer/passkit_pass/${passkitPassUid}.json`);
  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};



