/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import ShopService from 'services/shop';

export default {
  data() {
    return {
      shop: {},
      visits: [],
      blackList: ['id','uid','business_client_id','created_at','updated_at'],
    }
  },
  methods: {
    fetch() {
      store.dispatch('setPageTitle', '');
      store.dispatch('setLoading', true);

      ShopService.get(Vue.router.currentRoute.params.id)
      .then((res) => {
        this.shop = res.shop;
        this.visits = res.visits;
        store.dispatch('setLoading', false);
      })
    }
  },
  created() {
    this.fetch();
  },
  filters: {
    formatDate: function(date) {
      return moment(date).format('lll');
    }
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VInfo: require('components/info/component.vue'),
  },
};



