import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = (error) => {
  store.dispatch('setErrorMessage', { type: 'danger', messages: error.messages });
};

export default (uid, tmpStampCard) => {
  const stampCard = {
    title: tmpStampCard.title,
    first_name: tmpStampCard.first_name,
    last_name: tmpStampCard.last_name,
    email: tmpStampCard.email,
    telephone: tmpStampCard.telephone,
  };
  const request = Vue.$http.patch(`/employee/${store.state.auth.currentUser.businessClientKey}/stamp_cards/${uid}.json`, { stamp_card: stampCard });
  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};



