/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

export default {
  data() {
    return {
      languageThImg: require("images/th.png"),
      languageEnImg: require("images/us.png"),
      currentLanguage: '',
    }
  },
  props: {
    language: { // send languageService to here
      type: Object,
      required: true,
    }
  },
  methods: {
    setCurrentLanguage(_language) {
      this.language.set(_language);
      this.currentLanguage = _language;
    }
  },
  mounted() {
    this.setCurrentLanguage(this.language.get());
  }
};



