/* ============
 * Login Index Page
 * ============
 *
 * Page where the user can login
 */

import AuthService from './../../../services/auth';
import store from 'app/store';
const image = require("images/cnt-logo.png");


export default {
  data() {
    return {
      employee: {
        email: '',
      },
      logoImg: image,
    };
  },
  methods: {
    subminForm(employee) {
      if (!employee.email) {
        store.dispatch('setErrorMessage', { type: 'danger', messages: [this.$t("alertMessage.forgotPassword.emailCannotBeBlank")] });
        return false;
      }
      AuthService.resetPassword(employee);
    },
  },
  components: {
    VLayout: require('layouts/login/login.vue'),
    VAlert: require('components/alert/component.vue'),
  },
};



