/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import RedeemService from 'services/redeem';
import languageService from 'services/language';

export default {
 data() {
   return {
     stampCard: {},
     shops: [],
     selectedShopUid: '',
   }
 },
 methods: {
   submitForm() {
     const stampCardUid = Vue.router.currentRoute.params.id;
     const redeem = { shop_uid: this.selectedShopUid };

     RedeemService.create(stampCardUid, redeem)
     .then((p) => {
       if (p.success) {
         Vue.router.push({ name: 'stampCard.show', params: { id: stampCardUid } })
         store.dispatch('setErrorMessage', { type: 'info', messages: [this.$t("alertMessage.stampCard.successfullyRedeemed")] });
       } else {
         store.dispatch('setErrorMessage', { type: 'danger', messages: [p.error] });
       }
     })
   },
   fetch() {
     store.dispatch('setLoading', true);
     RedeemService.get(Vue.router.currentRoute.params.id)
     .then((res) => {
       this.stampCard = res.stamp_card;
       this.shops = res.shops;
       if (res.shops.length > 0) {
         this.selectedShopUid = res.shops[0].uid;
       }
       store.dispatch('setLoading', false);
     })
   },
 },
 filters: {
   formatDate: function(date) {
    let language = languageService.get();
    return moment(date).locale(language).format('lll');
   }
 },
 mounted() {
   this.fetch();
 },
 watch: {
   '$route': {
     handler: function() {
       this.fetch();
     },
     deep: true
   }
 },
 components: {
   VPanel: require('components/panel/component.vue'),
   VStatusLabel: require('components/status_label/component.vue'),
 },
}



