import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = () => {
};

export default (uid) => {
  const request = Vue.$http.get(`/employee/${store.state.auth.currentUser.businessClientKey}/stamp_cards/${uid}/new_redeem.json`);
  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};

