/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';
import store from './../../store';

export default {
  mixins: [
    slotMixin,
  ],
  props: {
    contextType: {
      type: String,
      require: false,
      default: ""
    },
    messages: {
      type: Array,
      require: false,
      default: () => [],
    },
  },
  computed: {
    classNames() {
      let classNames = ['alert'];
      if (this.contextType) {
        classNames.push(`alert-${this.contextType}`);
      } else {
        classNames.push('alert-info');
      }
      return classNames;
    },
  },
  methods: {
    close() {
      store.dispatch('setErrorMessage', {});
    },
  }
};



