/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';
import languageService from 'services/language';

export default {
  mixins: [
    slotMixin,
  ],
  props: {
    data: {
      type: Object,
      required: false,
      default: () => {},
    },
    registrationFields: {
      type: Array,
      required: false,
      default: [],
    },
    blackListInfo: {
      type: Array,
      required: false,
      default: [],
    },
  },
  filters: {
    capitalize(value) {
      if (!value) return '';
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
    humanize(value) {
      return value
      .replace(/([A-Z])/g, ' $1')
      .replace(/^./, function(str){ return str.toUpperCase(); })
      .replace('_', ' ');
    }
  },
  methods: {
    allowKey(key) {
      return this.blackListInfo.indexOf(key) === -1;
    },
    fullName(){
      let fullName = [];
      if(this.data.title) fullName.push(this.data.title);
      if(this.data.firstName) fullName.push(this.data.firstName);
      if(this.data.lastName) fullName.push(this.data.lastName);
      fullName = fullName.join(" ");
      if(fullName) return fullName;
      return this.$t('members.noName');
    },
    showProfileImage() {
      if(!this.data.profileImage) return require("images/member-profile.png");
      return this.data.profileImage;
    },
    formatMonth() {
      if (!this.data.monthOfBirth) return this.$t('members.notAvailable');
      let monthOfBirth = parseInt(this.data.monthOfBirth) - 1;
      let language = languageService.get();
      return moment().month(monthOfBirth).locale(language).format("MMMM");
    },
    checkValue(value) {
      if(!value){
        return this.$t('members.notAvailable');
      }
      return value;
    }
  }
};



