/* ============
 * Account Index Page
 * ============
 *
 * Page where the user can view the account information
 */

import store from 'app/store';
import Vue from 'vue';
import StampCardService from 'services/stamp_card';

export default {
  data() {
    return {
      stampCard: {},
      titleOrigin: '',
      isGenerateMember: false,
      submitButtonTitle: this.$t("button.save"),
      headingTitle: this.$t("stampCard.editProfile"),
      registrationFields: [],
    };
  },
  methods: {
    submitForm(stampCard) {
      store.dispatch('setLoading', true);
      if(this.isGenerateMember){
        StampCardService.generateMember(Vue.router.currentRoute.params.id, stampCard)
        .then((p) => {
          store.dispatch('setLoading', false);
          if(!p) return;
          Vue.router.replace({ name: 'stampCard.generateMember', params: { id: Vue.router.currentRoute.params.id } });
          store.dispatch('setErrorMessage', { type: 'info', messages: p.messages });
        })
      }else{
        StampCardService.update(Vue.router.currentRoute.params.id, stampCard)
        .then((p) => {
          store.dispatch('setLoading', false);
          Vue.router.replace({ name: 'stampCard.show', params: { id: Vue.router.currentRoute.params.id } });
          store.dispatch('setErrorMessage', { type: 'info', messages: [this.$t("alertMessage.stampCard.profileSuccessFullyUpdated")] });
        })
      }
    },
    updateTitle(title){
      this.stampCard.title = title;
    },
    checkValueIsNull(value) {
      if (value == null) {
        return '';
      }
      return value;
    },
    headingTitleTranslate() {
      if(Vue.router.currentRoute.query.from && Vue.router.currentRoute.query.from === 'generate_member'){
        return this.headingTitle = this.$t("stampCard.register");
      }
      return this.headingTitle = this.$t("stampCard.editProfile");
    },
    submitButtonTitleTranslate() {
      if(Vue.router.currentRoute.query.from && Vue.router.currentRoute.query.from === 'generate_member'){
        return this.submitButtonTitle = this.$t("button.saveAndGenerateMember");
      }
      return this.submitButtonTitle = this.$t("button.save");
    },
    fetch() {
      store.dispatch('setLoading', true);
      StampCardService.get(Vue.router.currentRoute.params.id)
      .then((res) => {
        this.stampCard = res.stamp_card;
        this.registrationFields = res.registration_fields;
        this.titleOrigin = (this.stampCard.title ? this.stampCard.title : '');
        store.dispatch('setLoading', false);
      })
    },
  },
  created() {
    if(Vue.router.currentRoute.query.from && Vue.router.currentRoute.query.from === 'generate_member'){
      this.isGenerateMember = true;
      this.submitButtonTitle = this.$t("button.saveAndGenerateMember");
      this.headingTitle = this.$t("stampCard.register");
    }
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VTitleOther: require('components/title_other/component.vue'),
  },
};



