import Vue from 'vue';
import store from './../../store';

const success = (message) => {
  store.dispatch('logout', message);
  Vue.router.push({
    name: 'login.index',
  });
};

const failed = () => {
};

export default (message) => {
  Vue.$http.get('/employees/sign_out.json')
  .then(() => {
    success(message);
  })
  .catch(() => {
    failed();
  });
};



