/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

 import store from 'app/store';
 import Vue from 'vue';
 import StampTemplateService from 'services/stamp_template';
 import VQrcode from 'v-qrcode/src/index';

 export default {
   data() {
     return {
       stampTemplateName: '',
       stampImage: '',
       stampTemplateUid: '',
       qrCls: 'qr-block',
       qrText: '',
       qrSize: 171,
       qrPadding: 0,
       qrLevel: 'L',      // value can be one of L, Q, M, H
       qrType: 'image',
     }
   },
   methods: {
     fetch() {
       store.dispatch('setLoading', true);
       StampTemplateService.get(Vue.router.currentRoute.params.id)
       .then((res) => {
         this.stampImage = res.image;
         this.stampTemplateName = res.name;
         this.qrText = res.qr_link;
         this.stampTemplateUid = res.uid;
         store.dispatch('setLoading', false);
       })
     },
   },
   mounted() {
     this.fetch();
   },
   watch: {
     '$route': {
       handler: function() {
         this.fetch();
       },
       deep: true
     }
   },
   components: {
     VPanel: require('components/panel/component.vue'),
     VQrcode: VQrcode,
   },
 }



