/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */


export default {
  data(){
    return {
      titles: [
        {
          value: '',
          text: 'pleaseSelectTitle',
        },
        {
          value: 'Mr',
          text: 'mr',
        },
        {
          value: 'Mrs',
          text: 'mrs',
        },
        {
          value: 'Miss',
          text: 'miss',
        },
        {
          value: 'other',
          text: 'other',
        },
      ],
      vTitle: '',
      cTitle: '',
      otherTitle: false,
      first: '',
    }
  },
  props: {
    title: {
      type: String,
      required: true,
      default: '',
    },
    titleOrigin: {
      type: String,
      required: true,
      default: '',
    }
  },
  watch: {
    titleOrigin() {
      if(['Mr', 'Miss', 'Mrs', ''].indexOf(this.titleOrigin) !== -1){
        this.vTitle = this.titleOrigin;
      }
    },
    title() {
      if(['Mr', 'Miss', 'Mrs', ''].indexOf(this.title) === -1){
        this.vTitle = 'other';
      }
      this.cTitle = this.title;
    },
    cTitle() {
      this.checkSelectOther();
      if(this.cTitle !== this.title){
        this.$emit('title', this.cTitle);
      }
    },
  },
  methods: {
    updateTitle(newTitle) {
      if(['Mr', 'Miss', 'Mrs', ''].indexOf(newTitle) === -1){
        this.cTitle = '';
        this.otherTitle = true;
      }else{
        this.cTitle = newTitle;
      }
    },
    checkSelectOther() {
      if (this.vTitle == 'other') {
        this.otherTitle = true;
      } else {
        this.otherTitle = false;
      }
    },
  },
};



