import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = (error) => {
  store.dispatch('setErrorMessage', { title: 'Error', messages: [error] });
};

export default (uid, stampPoint) => {
  const request = Vue.$http.post(`/employee/${store.state.auth.currentUser.businessClientKey}/stamp_cards/${uid}/stamp_points/create.json`, stampPoint);

  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};

