/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import ShopService from 'services/shop';

export default {
  data() {
    return {
      shops: []
    }
  },
  methods: {
    fetch() {
      store.dispatch('setLoading', true);
      ShopService.list()
      .then((shops) => {
        store.dispatch('setLoading', false);
        this.shops = shops;
      })
    }
  },
  mounted() {
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
  }
};



