/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import MemberService from 'services/member';
import languageService from 'services/language';

export default {
  data() {
    return {
      member: {},
      visits: [],
      blackListInfo: ['uid', 'profileImage', 'timeZonePreference'],
      registrationFields: []
    }
  },
  created() {
    this.fetch();
  },
  methods: {
    getOfferName(value){
      if (!value) {
        return "No Offer";
      }
      return value;
    },
    fetch() {
      store.dispatch('setLoading', true);
      MemberService.get(Vue.router.currentRoute.params.id)
      .then((res) => {
        this.registrationFields = res.registration_fields;
        this.member = res.member;
        this.visits = res.visits;
        store.dispatch('setLoading', false);
      })
    },
  },
  filters: {
    formatDate: function(date) {
      let language = languageService.get();
      return moment(date).locale(language).format('lll');
    }
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VInfoProfile: require('components/info_profile/component.vue'),
  },
};



