/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import VisitTypeService from 'services/visit_type';

export default {
  data() {
    return {
      visit_types: [],
    }
  },
  methods: {
    fetch() {
      store.dispatch('setPageTitle', '');
      store.dispatch('setLoading', true);

      VisitTypeService.list()
      .then((res) => {
        this.visit_types = res;
        store.dispatch('setLoading', false);
      })
    }
  },
  created() {
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VStatusLabel: require('components/status_label/component.vue'),
  },
};



