/* ============
 * Login Index Page
 * ============
 *
 * Page where the user can login
 */

import AuthService from './../../../services/auth';
const image = require("images/cnt-logo.png");

export default {
  data() {
    return {
      employee: {
        password: '',
        password_confirmation: '',
      },
      logoImg: image,
      checkMinLengthPassword: false,
    };
  },
  methods: {
    summitForm(employee) {
      if(employee.password.length < 8){
        this.checkMinLengthPassword = true;
      } else {
        this.checkMinLengthPassword = false;
      }
      // AuthService.login(employee);
    },
  },
  components: {
    VLayout: require('layouts/login/login.vue'),
    VAlert: require('components/alert/component.vue'),
  },
};



