/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import MenuService from 'services/menu';
import CancelStampCardService from 'services/cancel_stamp_card';
import languageService from 'services/language';

export default {
 data() {
   return {
     stampCard: {},
   }
 },
 methods: {
   cancelStampCardForm() {
     const vmodal = this.$refs.modal;
     vmodal.hide();
     const stampCardUid = Vue.router.currentRoute.params.id;

     CancelStampCardService.create(stampCardUid)
     .then((p) => {
       if (p.success) {
         Vue.router.replace({ name: 'stampCard.show', params: { id: stampCardUid } })
         store.dispatch('setErrorMessage', { type: 'info', messages: [this.$t("alertMessage.stampCard.successfullyInactivated")] });
       } else {
         store.dispatch('setErrorMessage', { type: 'danger', messages: [p.error] });
       }
     })
   },
   fetch() {
     store.dispatch('setLoading', true);
     MenuService.get(Vue.router.currentRoute.params.id)
     .then((res) => {
       this.stampCard = res;
       store.dispatch('setLoading', false);
     })
   },
   formatDate(date) {
     let language = languageService.get();
     return moment(date).locale(language).format('ll HH:mm');
   }
 },
 mounted() {
   this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VModal: require('components/modal/component.vue'),
  },
}



