/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import ShopService from 'services/shop'
import VisitService from 'services/visit'
import ShopEmployeeOffer from 'services/shop_employee_offer'

export default {
  data() {
    return {
      visit: {},
      shops: [],
      visitTypes: [],
      offers: [],
      disableButton: true,
    }
  },
  created() {
    this.fetch();
  },
  methods: {
    submitForm(visit) {
      visit.memberUid = Vue.router.currentRoute.params.id;
      VisitService.create(visit)
      .then((p) => {
        if (p.success) {
          Vue.router.push({ name: 'member.show', params: { id: visit.memberUid } })
          store.dispatch('setErrorMessage', { type: 'info', messages: [this.$t("alertMessage.visitShop.successfullyUpdated")] });
        } else {
          store.dispatch('setErrorMessage', { type: 'danger', messages: p.messages });
        }
      })
    },
    fetchData() {
      ShopService.listVisitTypesOffers(this.visit.shopUid)
      .then((res) => {
        store.dispatch('setLoading', false);
        if (res.visit_types.length != 0) {
          this.visitTypes = res.visit_types;
          this.visit.visitTypeUid = res.visit_types[0].uid;
          this.disableButton = false;
          store.dispatch('setErrorMessage', { type: 'danger', messages: [] });
        } else {
          this.visitTypes = [];
          this.visit.visitTypeUid = [];
          this.disableButton = true;
          store.dispatch('setErrorMessage', { type: 'danger', messages: [this.$t("alertMessage.visitShop.cannotCreateVisitShop")] });
        }

        this.visit.offerUid = Vue.router.currentRoute.params.offerId;
        if (res.offers.length != 0) {
          this.offers = res.offers;
        } else {
          this.offers = [];
        }
      })
    },
    fetch() {
      store.dispatch('setPageTitle', '');
      store.dispatch('setLoading', true);
      ShopEmployeeOffer.shops(Vue.router.currentRoute.params.offerId)
      .then((shops) => {
        if (shops.employee_inactive_message) {
          store.dispatch('setLoading', false);
          Vue.router.push({ name: 'error.index', params: { message: shops.employee_inactive_message } })
        } else {
          this.shops = shops;
          if (this.shops.length > 0) {
            this.visit.shopUid = this.shops[0].uid;
          }
          this.fetchData();
        }
      })
    },
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
  },
}



