/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import ShopService from 'services/shop';
import VisitService from 'services/visit';
import ShopEmployeeOffer from 'services/shop_employee_offer';
import languageService from 'services/language';

export default {
  data() {
    return {
      offer: [],
      shops: [],
      visit: {},
      visitTypes: [],
      selectedShopUid: '',
    };
  },
  methods: {
    submitForm() {
      if (!this.selectedShopUid) {
        store.dispatch('setErrorMessage', {
          type: 'danger',
          messages: [this.$t('alertMessage.pass.pleaseSelectShop')],
        });
        return false;
      }
      if (!this.visit.visitTypeUid) {
        store.dispatch('setErrorMessage', {
          type: 'danger',
          messages: [this.$t('alertMessage.pass.cannotCreateVisitShop')],
        });
        return false;
      }

      const passkitPassUid = Vue.router.currentRoute.params.id;
      VisitService.createVisitWithOffer(
        passkitPassUid,
        this.selectedShopUid,
        this.visit,
        this.offer
      ).then(p => {
        if (p.success) {
          Vue.router.replace({
            name: 'pass.redeemed',
            params: { redeemedCode: p.redeemed_code },
            query: {
              json_url: `${process.env.API_LOCATION}/employee/${this.$store
                .state.auth.currentUser
                .businessClientKey}/visits/${p.uid}/json`,
            },
          });
          store.dispatch('setErrorMessage', {
            type: 'info',
            messages: [this.$t('alertMessage.visitShop.successfullyCreated')],
          });
        } else {
          store.dispatch('setErrorMessage', {
            type: 'danger',
            messages: p.messages,
          });
        }
      });
    },
    fetchData() {
      ShopService.listVisitTypesOffers(this.selectedShopUid).then(res => {
        store.dispatch('setLoading', false);
        if (res.visit_types.length != 0) {
          this.visitTypes = res.visit_types;
          this.visit.visitTypeUid = res.visit_types[0].uid;
        } else {
          this.visitTypes = [];
          this.visit.visitTypeUid = [];
        }
      });
    },
    fetch() {
      store.dispatch('setLoading', true);
      const passkitPassUid = Vue.router.currentRoute.params.id;
      ShopEmployeeOffer.get(
        Vue.router.currentRoute.params.offerId,
        passkitPassUid
      ).then(res => {
        this.offer = res;
        store.dispatch('setLoading', false);
      });

      ShopEmployeeOffer.shops(
        Vue.router.currentRoute.params.offerId
      ).then(shops => {
        if (shops.employee_inactive_message) {
          store.dispatch('setLoading', false);
          Vue.router.push({
            name: 'error.index',
            params: { message: shops.employee_inactive_message },
          });
        } else {
          this.shops = shops;
          // this.shops.unshift({uid: '', name: 'Please Select Shop'});
          if (this.shops.length > 0) {
            this.selectedShopUid = this.shops[0].uid;
          }
          this.fetchData();
        }
      });
    },
  },
  filters: {
    formatDate: function(date) {
      let language = languageService.get();
      return moment(date).locale(language).format('LLL');
    },
  },
  created() {
    this.fetch();
  },
  watch: {
    $route: {
      handler: function() {
        this.fetch();
      },
      deep: true,
    },
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VStatusLabel: require('components/status_label/component.vue'),
  },
};



