/* ============
 * Home Index Page
 * ============
 *
 * The home index page
 */

import store from 'app/store';
import Vue from 'vue';
import StampCardService from 'services/stamp_card';
import VQrcode from 'v-qrcode/src/index';

export default {
  data() {
    return {
      stampCard: {},
      stampPoints: [],
      blackListStampCard: [],
      blackListStampPoint: ['can_invalidate','uid'],
      qrCls: 'qr-block',
      qrText: '',
      qrSize: 171,
      qrPadding: 10,
      qrLevel: 'L',      // value can be one of L, Q, M, H
      qrType: 'image',
      registrationFields: [],
    }
  },
  methods: {
    generateMember() {
      if(!this.stampCard.email){
        // Email not exist redirect to edit profile
        Vue.router.push({ name: 'stampCard.editProfile', query: { from: 'generate_member' } });
      }else{
        store.dispatch('setLoading', true);
        StampCardService.generateMember(Vue.router.currentRoute.params.id)
        .then((p) => {
          store.dispatch('setLoading', false);
          if(!p) return;
          Vue.router.replace({ name: 'stampCard.generateMember', params: { id: Vue.router.currentRoute.params.id } });
          store.dispatch('setErrorMessage', { type: 'info', messages: p.messages });
        })
      }
    },
    fetch() {
      store.dispatch('setLoading', true);
      StampCardService.get(Vue.router.currentRoute.params.id)
      .then((res) => {
        this.registrationFields = res.registration_fields
        this.stampCard = res.stamp_card;
        this.stampPoints = res.stamp_points;
        this.qrText = res.stamp_card.qr_link;
        store.dispatch('setLoading', false);
      })
    },
  },
  mounted() {
    this.fetch();
  },
  watch: {
    '$route': {
      handler: function() {
        this.fetch();
      },
      deep: true
    }
  },
  components: {
    VPanel: require('components/panel/component.vue'),
    VStampCardShow: require('components/stamp_card_show/component.vue'),
    VStampPoints: require('components/stamp_points/component.vue'),
    VQrcode: VQrcode,
  },
}



