/* ============
 * Panel Component
 * ============
 *
 * A basic panel component.
 *
 * Gives an idea how components work.
 */

import slotMixin from './../../mixins/slot';

export default {
  mixins: [
    slotMixin,
  ],
  props: {
    data: {
      type: Object,
      required: false,
      default: {},
    },
    blackList: {
      type: Array,
      required: false,
      default: [],
    }
  },
  filters: {
    capitalize(value) {
      if (!value) return '';
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    },
    humanize(value) {
    return value
    .replace(/([A-Z])/g, ' $1')
    .replace(/^./, function(str){ return str.toUpperCase(); })
    .replace('_', ' ');
    },
  },
  methods: {
    allowKey(key) {
      return this.blackList.indexOf(key) === -1;
    },
  }
};



