import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = () => {
  store.dispatch('setErrorMessage', { type: 'danger', title: '', message: 'Cannot create visit' });
};

export default (passkitPassUid, shopUid, visitType, offer) => {
  const visit = {
    passkit_pass_uid: passkitPassUid,
    offer_uid: offer.uid,
    shop_uid: shopUid,
    visit_type_uid: visitType.visitTypeUid,
  };
  const businessClientKey = store.state.auth.currentUser.businessClientKey;
  const request = Vue.$http.post(`/employee/${businessClientKey}/visits/create_visit_with_offer.json`, visit);
  return request
  .then(response => response.data)
  .catch((error) => {
    failed(error);
  });
};



