import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = () => {
};

export default (keyword) => {
  const request = Vue.$http.post(`/employee/${store.state.auth.currentUser.businessClientKey}/members/search.json`, { keyword });
  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};



