import Vue from 'vue';
import store from './../../store';

// When the request fails
const failed = () => {
};

export default () => {
  const request = Vue.$http.get(`/employee/${store.state.auth.currentUser.businessClientKey}/shops.json`);
  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};



