import store from 'app/store';
import Vue from 'vue';

// When the request fails
const failed = () => {
};

export default () => {
  const request = Vue.$http.get(`/employee/${store.state.auth.currentUser.businessClientKey}/business_clients.json`);
  return request
   .then(response => response.data)
   .catch((error) => {
     failed(error);
   });
};



